var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('b-container',{staticClass:"mt-3"},[_c('h1',{staticClass:"font-weight-bold text-left mb-3"},[_vm._v("Editionsrichtlinien")]),_c('b-row',[_c('b-col',{attrs:{"cols":"7"}},[_c('div',{staticClass:"text-left"},[_c('p',[_vm._v(" Die Dokumente der Rechtsakten aus der Kanzlei Oskar Samek enthalten unterschiedliche Textarten, weshalb für die verschiedenen Textgruppen auch unterschiedliche Editionsprinzipien aufgestellt wurden, die hier mitgeteilt werden. Außerdem bestehen die Akten aus verschiedenen Materialitätstypen, wobei der Großteil als Typoskripte überliefert ist. Darüber hinaus finden sich Handschriften sowie zahlreiche maschinen- und handschriftlich ausgefüllte vorbedruckte Formulare.")]),_c('p',[_vm._v("Die umfangreichen Bearbeitungen des Karl Kraus-Archivs durch die Jahrzehnte hindurch führten dazu, dass ein kleiner Teil der Dokumente nur noch als Kopien in den Mappen selbst vorhanden ist. Ziel war es aber, den Rechtsaktenbestand so wiederzugeben, wie er derzeit vorliegt.")]),_c('p',{staticClass:"font-weight-bold"},[_vm._v("Grundlage der edierten Dokumente")]),_c('p',[_vm._v("Als Textgrundlage wurde generell das veröffentlichte bzw. abgeschickte Dokument verwendet. Meistens liegt in den Rechtsakten nur bei den empfangenen Dokumenten der gegnerischen Seite bzw. der Gerichte das tatsächlich abgeschickte Dokument vor. Aus der Samek-Kanzlei sind in der Regel nur die Durchschläge der abgeschickten Dokumente, mitunter auch nur Vorstufen bzw. Fassungen, überliefert. Aus diesem Grund konnte nicht immer mit Sicherheit festgestellt werden, welcher Brief, welche Klage- oder Berufungsschrift tatsächlich abgeschickt wurde. Liegt das Dokument in mehreren, voneinander abweichenden Exemplaren vor, wurden die Varianten ediert, da diese den mitunter aufwendigen Arbeitsprozess und das Zwiegespräch zwischen Anwalt und Klient abbilden können. In diesen Fällen bildet die Textgrundlage dasjenige Exemplar, das den weitest fortgeschrittenen Arbeitsprozess darstellt. Auf Differenzen wird im Variantenapparat verwiesen. Sind die Textunterschiede zweier Exemplare erheblich, wurden beide Textzeugen ediert. ")]),_c('p',[_vm._v("Dokumentabschriften sind nur dann ediert, wenn sie das einzige überlieferte Exemplar darstellen. Dabei werden Schreib- bzw. Tippfehler ignoriert, da nicht die Fehleranfälligkeit der Schreiber:innen dokumentiert werden sollte. Gleiches Verfahren wurde bei den juristischen Dokumenten aus den Gerichten, etwa bei Urteilsschriften, angewendet, da hier kein Autor, wie es bspw. bei Briefen der Fall ist, zu bestimmen war, und diese Edition primär den Informationsgehalt des Urteils vermitteln soll.")]),_c('p',[_vm._v("Innerhalb der Projektlaufzeit blieb nach Abschluss der Kodierung lediglich Zeit für einen Lektorats- bzw. Korrektoratsdurchgang.")]),_c('p',{staticClass:"font-weight-bold"},[_vm._v("Allgemeine Transkriptionsregeln")]),_c('p',[_vm._v("Die Dokumente wurden zeilengenau transkribiert. Bei mehrspaltigem Satz werden die Spalteninhalte durch Absätze voneinander getrennt.")]),_c('p',[_vm._v("Späte Änderungen, die oft handschriftlich erfolgt sind, wurden stets berücksichtigt, da sie die Korrekturabläufe transparent machen und zeigen, dass Anwalt und Mandant, mitunter auch sehr intensiv, an den Texten im Nachhinein gearbeitet haben. Tippfehler, die sofort geändert wurden, sind hingegen nicht ediert, da nicht die Schreibfähigkeit der Schreiber:innen dokumentiert werden soll. Falsche Schreibweisen von Namen wurden allerdings konsequent wiedergegeben. Grundsätzlich wurden nur den Wortlaut betreffende sowie spätere handschriftliche interpunktionelle und orthografische Änderungen wiedergegeben, Sofortänderungen der Orthografie und Interpunktion wurden hingegen nicht aufgenommen.")]),_c('p',[_vm._v("Der Briefkopf in Korrespondenzen (der neben dem Datum, den Adressen der am Schriftstück beteiligten Personen ebenso einen Betreff und eine Diktatsigle enthalten kann) wurde in den Metadaten erfasst, nicht aber transkribiert. Stempel der gerichtlichen Dokumente wurden nicht transkribiert, aber in den Metadaten erfasst.")]),_c('p',[_vm._v("Sperrungen und Unterstreichungen sind grundsätzlich als solche differenziert wiedergegeben, weil beide Hervorhebungen mitunter auch im selben Exemplar vorkommen können. Anführungszeichen sind einheitlich und typografisch korrekt (also nicht mimetisch) wiedergegeben, um die Lesbarkeit der Dokumente, in denen häufig zitiert wurde, zu gewährleisten. Auch die zum Teil gebräuchliche Schreibweise bei Preisangaben durch zwei Bindestriche wurde durch ein Halbgeviert angepasst. Verschleifungen bei Unterschriften wurden aufgelöst. Seiten- und Blattzählungen, Dittografien und Kustoden werden nicht wiedergegeben. Spatien zwischen Abkürzungen wurden einheitlich nicht gesetzt. Unvollständige Datumsangaben bzw. Teile davon wurden ergänzt, da diese bei Durchschlägen oft fehlen, auf dem Typoskript allerdings auf dem vorbedruckten Briefpapier vorhanden waren. Eigentümliche Schreibweisen wie bspw. die Verwendung eines Versal-„J“ anstelle des Versal-„I“ wurden normalisiert. Editorische Hinzufügungen wurden mit eckigen Klammern gekennzeichnet. Für unleserliche Buchstaben und Wörter sind (mehrere) „[¿]“ gesetzt.")]),_c('p',{staticClass:"font-weight-bold"},[_vm._v("Schreibmaterial")]),_c('p',[_vm._v("Wurde in einem maschinell erstellten Dokument handschriftlich geändert, so ist dieser Wechsel des Schreibmaterials in der Transkription dargestellt sowie in den Metadaten genauer beschrieben, indem Schreiberhände und Schreibmaterial mitgeteilt sind. Bei nachträglichen handschriftlichen Hinzufügungen am Dokument wurde außerdem in der Darstellung unterschieden, ob es sich um eine handschriftliche Notiz bzw. Hervorhebung durch Marginalien am Rande handelt oder ob tatsächlich eine Änderung am Text vorgenommen wurde. Handschriftlich gesetzte Unterschriften wurden nicht als solche extra ausgezeichnet. Sogenannte Kanzleivermerke, die in der Regel ebenfalls handschriftlich und meistens am Ende eines Dokuments angebracht wurden, sind einheitlich differenziert in der Transkription wiedergegeben.")]),_c('p',[_vm._v("Durch die synoptische Darstellung ist die Transkription an jeder Stelle mit dem Original überprüfbar.")]),_c('p',{staticClass:"font-weight-bold"},[_vm._v("Nicht transkribierte Dokumente und Akten")]),_c('p',[_vm._v("Sämtliche Zeitungstexte, Originalmappen, -umschläge und Aktenvermerke sowie Kostenbestimmungen, Rückscheine, Rechnungen, Ladungen und weitere vorbedruckte Dokumente wurden nicht transkribiert, sondern mittels Faksimile dargestellt und durch die Angabe ihrer Metadaten erfasst. Die von Kraus und Samek angelegten Aktensammlungen zu Emmerich Békessy (Akten 43–59) wurden nicht ediert, da es sich hier meistens um Materialien zu Prozessen anderer Anwälte und ihrer Klienten handelt. Die Akten 204–215, bei denen es sich primär um separat abgelegte Kontextmaterialien handelt, wurden ebenfalls nicht ediert.")]),_c('p',{staticClass:"font-weight-bold"},[_vm._v("Materialitäts- und Dokumentstypen")]),_c('p',[_vm._v("Allen edierten Dokumenten wurden in den Metadaten Materialitäts- und Dokumentstypen zugeordnet, die auf einer für das Projekt und den Aktenbestand speziell erarbeiteten Taxonomie basieren. Liegt ein Dokument in der Mappe nur als archivarische Kopie vor, wurde der Materialitätstyp „Kopie“ ergänzt.")]),_c('p',{staticClass:"font-weight-bold"},[_vm._v("Intertextualität und weitere Referenzen")]),_c('p',[_vm._v("Der Projektausrichtung und zentralen Forschungsfrage gemäß wurden im transkribierten Text mehrere Auszeichnungen vorgenommen. Alle identifizierbaren Werke und Zitate aus Rechtstexten, die in "),_c('a',{attrs:{"target":"_blank","rel":"noopener noreferrer","href":"https://alex.onb.ac.at/"}},[_c('i',[_vm._v("ALEX")])]),_vm._v(" (Service der Österreichischen Nationalbibliothek) mit Digitalisaten zu historischen Rechts- und Gesetzestexten enthalten sind, wurden referenziert. Nicht ermittelte Werke und Zitate wurden als solche ausgezeichnet. Außerdem wurden Zitate und Werke aus der "),_c('a',{attrs:{"target":"_blank","rel":"noopener noreferrer","href":"https://fackel.oeaw.ac.at"}},[_c('i',[_vm._v("Fackel")])]),_vm._v(" seitengetreu referenziert. Darüber hinaus sind Zitate aus anderen, in den Rechtsakten enthaltenen Dokumenten bzw. die Dokumente selbst ausgezeichnet und referenziert.")]),_c('p',[_vm._v("Ein Sonderfall der Auszeichnung stellen jene in den Akten enthaltenen Textpassagen dar, die zu einem späteren Zeitpunkt in der "),_c('i',[_vm._v("Fackel")]),_vm._v(" veröffentlicht wurden und somit Teil eines literarischen Textes wurden. Diese sind, soweit sie identifizierbar waren, seitengenau referenziert.")]),_c('p',[_vm._v("In den Transkriptionen werden neben den Intertextualitätsreferenzen weitere Entitäten wie Personen, Orte, Institutionen ausgezeichnet.")]),_c('p',[_vm._v("Für Beispiele zu den beschriebenen Auszeichnungen "),_vm._v("s. PDF"),_vm._v(" zu den textkritischen Zeichen (folgt).")])])]),_c('b-col',{attrs:{"cols":"5"}},[_c('Zitierempfehlung')],1)],1)],1)}
